<template>
    <quill-editor ref="myQuillEditor" :options="editorOption" v-model="editordata.editorTxt" ></quill-editor>
</template>

<script>
    import 'quill/dist/quill.core.css'
    import 'quill/dist/quill.snow.css'
    import 'quill/dist/quill.bubble.css'
    import {quillEditor, Quill} from 'vue-quill-editor'
    import {container, ImageExtend, QuillWatch} from 'quill-image-extend-module'
    Quill.register('modules/ImageExtend', ImageExtend)
    export default {
        name: "testeditor",
        data(){
            return{
                // 
                editorOption: {
                    placeholder:this.editordata.hasOwnProperty('placeholder')?this.editordata.placeholder:'',
                    modules: {
                        ImageExtend: {
                            loading: true,
                            name: 'img',
                            action: '/UpLoad/Uploadpic',
                            // response 
                            // {code: 200; data:{ url: 'baidu.com'}}
                            //  return res.data.url
                            response: (res) => {
                                if(res.status==0){
                                    return res.data.img;
                                }else{
                                    console.log(res);
                                    return ''
                                }
                            },
                            header:(xhr)=>{
                                console.log(xhr)

                            },
                            // xhr.setRequestHeader('myHeader','myValue')
                            // formData.append('token', 'myToken')
                            //  headersformData
                            change:(xhr, formData)=> {
                                console.log(xhr)
                            }
                        },
                        // 
                        toolbar: {
                            container: container, //container
                            handlers: {
                                'image': function () {
                                    QuillWatch.emit(this.quill.id)
                                }
                            }
                        }
                    }
                },
            }
        },
        components:{quillEditor},
        props:['editordata'],
        mounted(){
            this.editorOption.placeholder='12456'
        }
    }
</script>

